import React from 'react';
import rgbHex from 'rgb-hex';
import './App.css';


class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      array: [],
      size: 40,
      divisor: 23,
    }
  }

  componentWillMount() {
    const generateArray = [];
    for (let i = 1; i < this.state.size + 1; i = i + 1) {
      generateArray.push(i);
    }
    this.setState({
      array: generateArray,
    })
  }

  changeSize = (increase) => {
    if (increase) {
      this.setState(prevState => {
        prevState.array.push(prevState.size + 1);
        return {
          size: prevState.size + 1,
          array: prevState.array,
        }
      });
    } else {
      this.setState(prevState => {
        prevState.array.pop();
        return {
          size: prevState.size - 1,
          array: prevState.array,
        }
      });
    }
  }

  calculateMod = (n, m) => {
    return ((n*m) % (this.state.divisor));
  }

  calculateColor = (k, min, max) => {
    if (max - min === 0) return rgbHex(0, 0, 0, 0);
    else {
      const shade = Math.min((k / (2*(max - min))), 0.5);
      return rgbHex(0, 0, 0, shade);
    }

  }

  renderRow = (n) => {
    const calculatedArray = this.state.array.map(m => this.calculateMod(n, m));
    const forSorting = Object.assign([], calculatedArray);
    forSorting.sort((a, b) => (a-b));
    const min = forSorting[0];
    const max = forSorting[forSorting.length - 1];
    return (
      <>
        {calculatedArray.map(k => {
          return <div className="box" style={{backgroundColor: '#' + this.calculateColor(k, min, max)}}>{k}</div>
        })}
      </>
    )
  }

  render() {
    return (
      <div className="App">
        <div className="control">
          <div className="size">Size of table: {this.state.size}</div>
          <button onClick={() => this.changeSize(true)}>+</button>
          <button onClick={() => this.changeSize(false)}>-</button>
          <div className="divisor">Divisor: </div>
          <input value={this.state.divisor} onChange={(e) => this.setState({divisor: e.target.value})} type="number"></input>
        </div>
        <div className="grid">
          <div className="row">
            <div className="box"> </div>
            {this.state.array.map(n => {
              return <div className="box header top" key={n}>{n}</div>
            })}
          </div>
          {this.state.array.map(n => {
            return (
              <div className="row" key={n}>
                <div className="box header side">{n}</div>
                {this.renderRow(n)}
              </div>
            )
          })}
        </div>
      </div>
    );
  }

}

export default App;
